.projects-page .text-zone p{margin-bottom:20px;color:#fff;font-size:17px;font-family:sans-serif;font-weight:300;max-width:100%}.filter-container{margin-top:30px;display:flex;flex-wrap:wrap;gap:10px}.filter-container .filter-btn{padding:8px 16px;background:rgba(0,255,127,.1);border:1px solid rgba(0,255,127,.3);border-radius:20px;color:#fff;font-size:14px;font-family:"Helvetica Neue",sans-serif;cursor:pointer;transition:all .3s ease}.filter-container .filter-btn:hover{background:rgba(0,255,127,.2);border-color:rgba(0,255,127,.5)}.filter-container .filter-btn.active{background:#00ff7f;color:#000;border-color:#00ff7f}.projects-container{position:absolute;right:5%;top:50%;transform:translateY(-50%) translateX(2%);width:50%;height:80%;overflow-y:auto;padding-right:20px}.projects-container::-webkit-scrollbar{width:8px}.projects-container::-webkit-scrollbar-track{background:hsla(0,0%,100%,.1);border-radius:4px}.projects-container::-webkit-scrollbar-thumb{background:#00ff7f;border-radius:4px}.projects-container::-webkit-scrollbar-thumb:hover{background:#ff4500}.projects-grid{display:grid;grid-template-columns:repeat(auto-fit, minmax(350px, 1fr));gap:30px;padding:20px 0}.project-card{background:rgba(0,255,127,.05);border:1px solid rgba(0,255,127,.2);border-radius:15px;overflow:hidden;transition:all .3s ease;animation:fadeInUp .6s ease-out both;backdrop-filter:blur(10px)}.project-card:hover{background:rgba(0,255,127,.1);border-color:rgba(0,255,127,.4);transform:translateY(-5px);box-shadow:0 15px 35px rgba(0,255,127,.2)}.project-images{position:relative;height:200px;overflow:hidden;background:rgba(0,0,0,.3)}.project-images .image-carousel{height:100%;display:flex}.project-images .image-carousel img{width:100%;height:100%;object-fit:cover;transition:opacity .3s ease}.project-content{padding:25px}.project-header{margin-bottom:15px}.project-header .project-meta{display:flex;gap:15px;margin-bottom:10px;flex-wrap:wrap}.project-header .project-meta .category{background:rgba(0,255,127,.2);color:#00ff7f;padding:4px 12px;border-radius:12px;font-size:12px;font-weight:500;text-transform:uppercase;letter-spacing:.5px}.project-header .project-meta .year{background:rgba(255,69,0,.2);color:#ff4500;padding:4px 12px;border-radius:12px;font-size:12px;font-weight:500}.project-header .project-meta .status{padding:4px 12px;border-radius:12px;font-size:12px;font-weight:500;text-transform:uppercase}.project-header .project-meta .status.completed{background:rgba(0,255,0,.2);color:lime}.project-header .project-meta .status.in-progress{background:rgba(255,255,0,.2);color:#ff0}.project-header .project-meta .status.planned{background:rgba(128,128,128,.2);color:gray}.project-header .project-title{font-size:20px;font-weight:600;color:#fff;margin:0 0 5px 0;font-family:"Coolvetica",sans-serif;line-height:1.3}.project-header .company{font-size:14px;color:#00ff7f;margin:0;font-weight:500;font-family:"Helvetica Neue",sans-serif}.project-description{color:#ccc;font-size:14px;line-height:1.6;margin-bottom:20px;font-family:"Helvetica Neue",sans-serif}.project-features{margin-bottom:20px}.project-features h4{color:#fff;font-size:14px;margin:0 0 10px 0;font-weight:600;font-family:"Helvetica Neue",sans-serif}.project-features ul{list-style:none;padding:0;margin:0}.project-features ul li{position:relative;padding-left:15px;margin-bottom:5px;color:#ccc;font-size:13px;line-height:1.4;font-family:"Helvetica Neue",sans-serif}.project-features ul li:before{content:"";position:absolute;left:0;color:#00ff7f;font-size:10px;top:2px}.technologies{display:flex;flex-wrap:wrap;gap:8px;margin-bottom:20px}.technologies .tech-tag{background:hsla(0,0%,100%,.1);color:#fff;padding:5px 10px;border-radius:15px;font-size:11px;font-weight:500;border:1px solid hsla(0,0%,100%,.2);transition:all .3s ease;font-family:"Helvetica Neue",sans-serif}.technologies .tech-tag:hover{background:hsla(0,0%,100%,.2);border-color:hsla(0,0%,100%,.4)}.project-links{display:flex;gap:15px;align-items:center;flex-wrap:wrap}.project-links .project-link{display:flex;align-items:center;gap:8px;color:#00ff7f;text-decoration:none;font-size:14px;font-weight:500;transition:color .3s ease;font-family:"Helvetica Neue",sans-serif}.project-links .project-link:hover{color:#ff4500}.project-links .project-link svg{font-size:16px}.project-links .private-indicator{display:flex;align-items:center;gap:8px;color:gray;font-size:14px;font-weight:500;font-family:"Helvetica Neue",sans-serif}.project-links .private-indicator svg{font-size:16px}@keyframes fadeInUp{from{opacity:0;transform:translateY(30px)}to{opacity:1;transform:translateY(0)}}@media screen and (max-width: 1200px){.projects-grid{grid-template-columns:repeat(auto-fit, minmax(300px, 1fr));gap:25px}}@media screen and (max-width: 768px){.projects-container{position:relative;right:auto;top:auto;transform:none;width:100%;height:auto;margin-top:20px;padding:0 20px}.projects-grid{grid-template-columns:1fr;gap:20px;padding:10px 0}.project-card{margin-bottom:20px}.projects-page .text-zone{position:relative;left:auto;top:auto;transform:none;width:100%;margin-bottom:30px}.projects-page .text-zone .filter-container{margin-top:20px}.project-content{padding:20px}.project-header .project-meta{gap:10px}.project-links{gap:10px}}@media screen and (max-width: 480px){.projects-container{padding:0 15px}.project-content{padding:15px}.project-header .project-title{font-size:18px}.filter-container .filter-btn{padding:6px 12px;font-size:12px}}